<template>
    <view>
        <view v-if="hasLogin" class="hello">
           
        </view>
		<view class="maintop-bg">
			<view class="topbox">
				<view class="topli">今日</view>
				<view class="topli">昨日</view>
				<view class="topli">本周</view>
				<view class="topli">其他</view>
			</view>
			
		</view>
		
		
		<view class="row h80">
			<view class="left" style="width: 60%;padding-left: 20upx;">收入统计</view>
		</view>
		
		<view class="row ulist">
			<view class="left" style="width: 60%;padding-left: 20upx;">总计</view>
			<view class="right tr">1542</view>
		</view>
		<view class="row ulist" v-for="item in boxs">
			<view class="icon">
				<image src="../../static/img/homeHL.png"></image>
			</view>
			<view class="left">{{item.name}}</view>
			<view class="right tr">{{item.money}}</view>
		</view>
		 <view class="title">
		    您好 {{userName}}，您已成功登录。
		</view>
		
    </view>
</template>

<script>
    import {
        mapState
    } from 'vuex'

    export default {
        computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
		data() {
			return {
				boxs:[{
					name:"今日销售" ,
					money:12
				},{
					name:"今日销售" ,
					money:12
				},{
					name:"今日销售" ,
					money:12
				},{
					name:"今日销售" ,
					money:12
				},{
					name:"今日销售" ,
					money:12
				},{
					name:"今日销售" ,
					money:12
				}]
				
			};
		},
        onLoad() {
            if (!this.hasLogin) {
                uni.showModal({
                    title: '未登录',
                    content: '您未登录，需要登录后才能继续',
                    /**
                     * 如果需要强制登录，不显示取消按钮
                     */
                    showCancel: !this.forcedLogin,
                    success: (res) => {
                        if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
                            if (this.forcedLogin) {
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            } else {
                                uni.navigateTo({
                                    url: '../login/login'
                                });
                            }
                        }
                    }
                });
            }
        }
    }
</script>

<style lang="scss">
    .hello {
        display: flex;
        flex: 1;
        flex-direction: column;
    }

    .title {
        color: #8f8f94;
        margin-top: 50upx;
    }

    .ul {
        font-size: 30upx;
        color: #8f8f94;
        margin-top: 50upx;
    }

    .ul>view {
        line-height: 50upx;
    }
	
	.maintop-bg{
		background:linear-gradient(0deg,rgb(155, 216, 247),rgb(15, 174, 255) 100%);
		height: 240upx;
		
	}
	.topbox{
		border: 1px solid #fff;
		display: flex;
		width: 80%;
		border-right:8upx;
		margin: auto;
		
	}
	.topli{
		border-right: 1px solid #fff;
		text-align: center;
		width: 25%;
		color: #fff;
		line-height: 40upx;
		font-size: 24upx;
		
	}

		
</style>
